body {
  font-family: "Work Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000;
  font-size: 2rem;
}

.contain {
  width: 95%;
  max-width: 140rem;
  margin: 2rem auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Work Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
