.alert-error {
  background: #CB7766;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  transition: background-color 0.3s ease-out;
  border: none;
  margin-top: 0.7142857143rem;
  padding: 1rem;
}
.alert-error h3 {
  margin: 0;
  font-weight: 700;
}

.alert-success {
  background: #39A900;
  color: #FFFFFF;
  text-decoration: none;
  text-align: center;
  transition: background-color 0.3s ease-out;
