header h1 {
  color: #FFFFFF;
  margin: 0;
}
header .modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #000;
  opacity: 0.95;
  transition: all 0.3s ease-out;
}
header .modal .close {
  display: flex;
  width: 70%;
  position: absolute;
  top: 10rem;
}
header .modal .close button {
  font-size: 2rem;
  color: #FFFFFF;
  margin-left: auto;
}
header .modal .nav__link {
  width: 70%;
}
header .modal .nav__link li {
  display: block;
  transition: all 0.3s ease-out;
  border-radius: 0.3rem;
}
header .modal .nav__link li a {
  display: block;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  padding: 2rem;
}
header .modal .nav__link li:hover {
  background-color: rgba(255, 255, 255, 0.3882352941);
}
header .modal.off {
  opacity: 0;
  z-index: -1;
}
header #menu {
  cursor: pointer;
}
header .splide .splide__track {
  height: 60rem;
}
header .splide .splide__track .splide__list .splide__slide {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
header .splide .splide__track .splide__list .splide__slide.slide-1 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide2.jpeg");
}
header .splide .splide__track .splide__list .splide__slide.slide-2 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide1.jpeg");
}
header .splide .splide__track .splide__list .splide__slide.slide-3 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide3.jpeg");
}
header.absolute .nav {
  position: absolute;
}
header.relative {
  background-color: #00324D;
  padding: 2rem;
}
header.relative .nav {
  position: relative;
  margin: 0 auto;
}
.nav {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 3rem;
  margin-top: 2rem;
  z-index: 1;
}
.nav .nav__link {
  display: none;
  margin-left: auto;
}
.nav .nav__link .nav__item {
  list-style: none;
  font-size: 1.45rem;
}
.nav .nav__link .nav__item a {
  padding: 1.25rem 2.25rem;
  border-radius: 0.5rem;
  color: #FFFFFF;
  transition: background-color 0.3s ease-out;
}
.nav .nav__link .nav__item a:hover {
  background-color: rgba(255, 255, 255, 0.3882352941);
}
@media (min-width: 768px) {
  .nav .nav__link {
    display: flex;
    gap: 1rem;
  }
}
.nav img {
  height: 4rem;
  width: 4rem;
  margin-left: auto;
}
@media (min-width: 768px) {
  .nav img {
    display: none;
  }
}

main.inicio {
  margin-top: 5rem;
}
main.inicio .concursos {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding: 1rem;
}
main.inicio .concursos .concurso {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
main.inicio .concursos .concurso .concurso-informacion h1 {
  text-align: left;
}
main.inicio .concursos .concurso .concurso-informacion p {
  text-align: left;
}
main.inicio .concursos .concurso:nth-of-type(even) {
  direction: rtl;
}
@media (min-width: 1024px) {
  main.inicio .concursos .concurso {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
main.inicio .concursos-cards {
  padding: 5rem;
}
main.inicio .concursos-cards h1 {
  margin-top: 0;
}
main.inicio .concursos-cards .cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
}
main.inicio .concursos-cards .cards .card {
  display: flex;
  border-radius: 0.75rem;
  flex-direction: column;
  width: 35rem;
  padding: 3rem;
  height: 40rem;
  background-color: #FFFFFF;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
main.inicio .concursos-cards .cards .card button {
  margin-top: auto;
  border-radius: 0.5rem;
}
main.inicio .ponentes .splide .splide__track .splide__slide {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  gap: 2rem;
  padding: 5rem 0;
}
main.inicio .ponentes .splide .splide__track .splide__slide div {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 70%;
  margin: auto;
}
main.inicio .ponentes .splide .splide__track .splide__slide div h1 {
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  main.inicio .ponentes .splide .splide__track .splide__slide div h1 {
    text-align: left;
  }
}
main.inicio .ponentes .splide .splide__track .splide__slide div p {
  text-align: center;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  main.inicio .ponentes .splide .splide__track .splide__slide div p {
    text-align: left;
  }
}
main.inicio .ponentes .splide .splide__track .splide__slide img {
  height: 12rem;
  width: 12rem;
}
@media (min-width: 768px) {
  main.inicio .ponentes .splide .splide__track .splide__slide {
    grid-template-columns: 2fr 1fr;
  }
}

section:nth-of-type(even) {
  background-color: #F3F4F4;
}
@media (min-width: 768px) {
  section {
    padding: 2rem;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 4rem;
  background-color: #39A900;
}
.footer .nav--footer {
  position: relative;
  margin: 0;
}
.footer .nav--footer .nav__link--footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
}
.footer .nav--footer .nav__link--footer .nav__item {
  width: 100%;
}
.footer .nav--footer .nav__link--footer .nav__item a {
  display: block;
  padding: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer .nav--footer .nav__link--footer .nav__item a {
    display: inline;
  }
}
@media (min-width: 768px) {
  .footer .nav--footer .nav__link--footer .nav__item {
    width: auto;
  }
}
@media (min-width: 768px) {
  .footer .nav--footer .nav__link--footer {
    flex-direction: row;
    gap: 2rem;
    margin: 0;
  }
}
.footer .footer__social {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer {
    flex-direction: row;
  }
}

/*Estilos para el formulario*/
.seccion {
  display: none;
}

.categorias {
  margin: 4rem 0;
}

.categorias_div {
  display: flex;
  margin: auto;
  flex-direction: column;
  border: 1px solid rgba(161, 160, 160, 0.699);
  border-radius: 5px;
  width: 85%;
}

.title-categorias {
  display: flex;
  justify-content: space-between;
  background: rgba(197, 229, 244, 0.3960784314);
  padding: 20px;
  margin-bottom: 20px;
}

.title-categorias h1 {
  font-family: "WorkSans-Medium";
  font-size: 1.3rem;
}

.descargar-formato {
  display: flex;
  justify-content: center;
  align-items: center;
}

.descargar-formato p {
  font-family: "WorkSans-Italic";
  margin-right: 15px;
}

.descargar-formato button {
  padding: 10px 30px;
  background: #39A900;
  border: none;
  border-radius: 5px;
  font-family: "WorkSans-Medium";
  font-size: 1.2rem;
  color: #fff;
  cursor: pointer;
  transition: 0.4s ease-in-out all;
}

.icon-descarga {
  width: 20px;
  filter: invert(100%);
}

.descargar-formato button:hover {
  transform: scale(1.1);
}

.form {
  display: flex;
  flex-direction: column;
}

.button-registro {
  padding: 10px 0;
  background: var(--azulSena);
  border: none;
  border-radius: 5px;
  font-family: "WorkSans-Medium";
  font-size: 1.2rem;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.button-registro:hover {
  background: #39a900;
}

/*Estilos para los input*/
.coolinput {
  display: flex;
  flex-direction: column;
  width: 90%;
  padding-bottom: 20px;
  margin: auto;
}

.coolinput label.text {
  font-size: 1.1rem;
  color: #39A900;
  font-weight: 700;
  position: relative;
  top: 0.5rem;
  margin: 0 0 0 7px;
  padding: 0 3px;
  border: 2px #39A900 solid;
  background: #E8F5FB;
  width: fit-content;
  font-family: "WorkSans-Italic";
}

.coolinput input[type=text].input, .coolinput input[type=email].input, .select {
  padding: 11px 10px;
  font-size: 1rem;
  border: 2px #39A900 solid;
  font-family: "WorkSans-Medium";
  border-radius: 5px;
  background: #F3F4F4;
}

.select {
  outline: none;
}

.coolinput input[type=text].input:focus, .coolinput input[type=email].input:focus {
  outline: none;
}

/*Fin de estilos input*/
/*estilo para el input tipo file*/
.form-title {
  font-size: 1.1rem;
  color: #39A900;
  font-family: "WorkSans-Medium";
}

.drop-container {
  background-color: #fff;
  position: relative;
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-top: 2.1875rem;
  border-radius: 10px;
  border: 2px dashed rgb(171, 202, 255);
  font-size: 1.1rem;
  font-family: "WorkSans-Medium";
  cursor: pointer;
  transition: background 0.2s ease-in-out, border 0.2s ease-in-out;
}

.drop-container:hover {
  background: #E8F5FB;
  border-color: rgba(17, 17, 17, 0.616);
}

.drop-title {
  font-size: 1.1rem;
  color: black;
  font-family: "WorkSans-Medium";
  text-align: center;
  transition: color 0.2s ease-in-out;
}

#file-input {
  width: 600px;
  max-width: 100%;
  color: #444;
  padding: 2px;
  background: #fff;
  border-radius: 10px;
  border: 1px solid rgba(8, 8, 8, 0.288);
}

#file-input::file-selector-button {
  margin-right: 20px;
  border: none;
  background: rgb(61, 186, 196);
  padding: 10px 20px;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

#file-input::file-selector-button:hover {
  background: #39A900;
}

/*fin de estilo para el input tipo file*/
/*Inicio de estilo para el apartado de ponentes*/
.div-ponente {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.input-ponente {
  width: 90%;
  margin: 7px;
}

/*mensajes de Error*/
.errores-archivos {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.error-archivo {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background: rgb(204, 119, 102);
  border-radius: 5px;
}

.error-archivo h1 {
  font-family: "WorkSans-Medium";
  margin-right: 20px;
  font-size: 1.1rem;
  color: var(--colorwhite);
}

.error-archivo p {
  font-family: "WorkSans-Italic";
  font-size: 1rem;
}

.message-register, .message-register-error {
  display: flex;
  width: 60%;
  margin: auto;
  justify-content: center;
  align-items: center;
}

.message-register {
  padding: 15px;
  border-radius: 5px;
  background: var(--verdeSena);
}

.message-register p {
  font-family: "WorkSans-Italic";
  font-size: 1.3rem;
  color: white;
}

.message-register span {
  font-family: "WorkSans-Medium";
  font-size: 1.5rem;
}

@media (max-width: 1024px) {
  .categorias_div {
    width: 90%;
  }
  .title-categorias {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .main h1 {
    font-size: 2rem;
    text-align: justify;
    margin-bottom: 20px;
  }
  .main p {
    font-size: 1.2rem;
  }
  .submenu-nav {
    padding: 10px 0;
  }
  .submenu-nav a {
    font-size: 1.2rem;
    text-decoration: underline;
  }
  .descargar-formato button {
    padding: 10px 20px;
    font-size: 1.1rem;
  }
  .icon-descarga {
    width: 18px;
  }
  .form-title {
    font-size: 1rem;
  }
}
@media (max-width: 390px) {
  .main h1 {
    font-size: 1.5rem;
  }
  .main p {
    font-size: 1rem;
  }
  .submenu-nav {
    flex-direction: column;
    padding: 10px 0;
  }
  .submenu-nav a {
    padding: 10px 0;
  }
  .title-categorias h1 {
    margin-bottom: 15px;
  }
  .descargar-formato {
    flex-direction: column;
  }
  .descargar-formato p {
    margin: 0;
  }
  .descargar-formato button {
    margin-top: 15px;
    width: 100%;
    padding: 12px;
    font-size: 1.1rem;
  }
  .coolinput label.text {
    font-size: 1rem;
  }
  .coolinput input[type=text].input, .coolinput input[type=email].input, .select {
    padding: 10px 10px;
    font-size: 0.9rem;
  }
}